.App {
  text-align: center;
}

.content {
  position: absolute;
  margin-top: 60px;
  padding-bottom: 30px;
  background: rgba(200,200,200,0.1);
  min-height: calc(100vh - 60px);
  width: 100vw;
}

.footer {
  position: fixed;
  bottom: 5px;
  right: 10px;
  font-size: 0.7rem;
  font-style: italic;
  text-align: right;
}

.footer > div {
  margin-top: 10px;
  width: auto;
  background: white;
}

.nav {
  position: fixed;
  top: 0;
  width: 100vw;
  height: 60px;
  background: black;
  z-index: 99;
}

.nav-flex {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  font-size: 1.2rem;
}

.nav-class {
  color: white;
  height: 100%;
  position: relative;
  display: inline-block;
  width: 170px;
  cursor: pointer;
}

.nav-dropdown-content {
  display: none;
  position: absolute;
  background-color: black;
  width: 100px;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  z-index: 1;
  width: 170px;
  font-size: 1rem;
}

.nav-dropdown-content a {
  color: white;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
}

.nav-dropdown-content a:hover {background-color:grey;}

.nav-class:hover .nav-dropdown-content {display: block;}

.nav-class:hover p {color: red;}

.task-interactive {
  margin: 20px 0;
  width: 100vw;
}

.task-btn {
  margin: 10px;
}

.task-btn button {
  height: 35px;
  width: 100px;
  font-size: 1.3rem;
  font-weight: bold;
}

.task-task {
  background: rgba(215, 183, 172, 0.5);
  width: 75%;
  margin: 15px auto;
  min-height: 20px;
}

.task-task > div{
  padding: 10px 50px;
}

.task-name {
  font-weight: bold;
  font-size: 1.5rem;
}

.task-desc {
  font-size: 1.2rem;
}

.task-title {
  font-size: 1.5rem;
}

.task-img img{
  height: 150px;
}

.task-content {
  padding: 0 100px;
}

